@charset "utf-8";
@import '../common/util';
@import "partials/normalize";

.kolonsport-site {
  .about-cont {
    margin: 78px 0 80px;
    h3 {
      font-size: 22px;
      color: #1f1f1f;
    }
    .about-wrap {
      margin-top: 60px;
      .about-list {
        margin-left: -20px;
        &::after {
          content: '';
          display: table;
          clear: both;
        }
        .list {
          display: block;
          color: #1f1f1f;
          position: relative;
          float: left;
          width: 310px;
          margin-left: 20px;
          margin-bottom: 60px;
          .item {
            margin-bottom: 20px;
          }
          .title {
            margin-bottom: 12px;
            overflow: hidden;
            text-overflow: ellipsis;
            display: block;
            word-wrap: break-word;
            white-space: nowrap;
            color: #1f1f1f;
            height: 18px;
            a {
              color: #1f1f1f;
              font-size: 15px;
              line-height: 1.4;
              text-overflow: ellipsis;
              white-space: nowrap;
              overflow: hidden;
              display: inline-block;
              font-weight: bold;
              max-width: 100%;
              white-space: normal;
              line-height: 1.2;
              height: 35px;
              word-wrap: break-word;
              display: -webkit-box;
              -webkit-line-clamp: 2;
              -webkit-box-orient: vertical;
            }
          }
        }
        &.wide-square-wrap {
          .list {
            width: 972px;
            margin-bottom: 17px;
            .item {
              height: 333px;
              overflow: hidden;
              margin-bottom: 17px;
              .sticky {
                display: none;
              }
              .txt-box {
                display: none;
              }
              .bg-image {
                height: 333px;
                padding: 0;
              }
            }
          }
        }
      }
      .list-more {
        margin: -10px auto 60px;
      }
    }
  }
  .about-detail {
    margin-top: 70px;
    h3.about-detail-title {
      margin-bottom: 15px;
      font-size: 18px;
      color: #1f1f1f;
      font-weight: bold;
    }
    .location {
      &>span, &>a {
        position: relative;
        display: inline-block;
        padding: 0 11px;
        font-size: 13px;
        color: #898989;
        &::before {
          content: '';
          position: absolute;
          left: 0;
          top: 1px;
          width: 1px;
          height: 12px;
          background-color: #e0e0e0;
        }
      }
      span {
        &:first-child {
          padding-left: 0;
          color: #1f1f1f;
          a {
            color: #1f1f1f;
          }
        }
        &:first-child::before {
          display: none;
        }
      }
      a:hover {
        text-decoration: underline;
      }
    }
    .page-function {
      a.download {
        font: 0 / 0 a;
        width: 16px;
        height: 18px;
        margin-right: 40px;
        background: url(/_ui/img/kolonsport/pc/icn/icn-download.png) no-repeat;
      }
    }
    .detail-cont {
      margin-top: 45px;
      margin-bottom: 45px;
      &.media-cont {
        background: #111111;
        padding: 65px 0;
        .detail-cont-wrapper {
          width: 1200px;
          margin: 0 auto;
        }
      }
      .detail-cont-wrapper {
        @include clear-content();
        text-align: center;
        line-height: 160%;
        img {
          margin: 0 auto;
        }
        .full-width > img {
          width: 100%;
        }
      }
      .detail-img {
        position: absolute;
        left: 0;
        top: 288px;
        right: 0;
        text-align: center;
        img {
          display: inline-block;
          width: 1200px;
          vertical-align: top;
        }
      }
      .nav {
        padding: 20px 0;
        text-align: center;
        background: #fff;
        border-bottom: 1px solid #e8e8e8;
        a {
          position: relative;
          display: inline-block;
          padding: 10px 25px;
          color: #aaa;
          font-size: 13px;
          &::before {
            content: '';
            position: absolute;
            left: 0;
            top: 12px;
            width: 1px;
            height: 10px;
            background-color: #f3f3f3;
          }
          &:first-child::before {
            display: none;
          }
          &.on {
            color: #1f1f1f;
          }
        }
      }
    }
    .buttons {
      margin : 80px auto 100px;
      text-align: center;
    }
  }
  .video-wrap {
    position: relative;
    width: 1200px;
    height: 675px;
    margin: 0;
    background-color: #000;
    iframe {
      width: 100% !important;
    }
    .poster {
      background-size: cover;
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
    }
    .video-play {
      position: absolute;
      width: 43px;
      height: 43px;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }
}
